.wrapper {
  overflow: hidden;
  position: relative;
  height: 40%;
  width: 450px;
  min-height: 300px;
  max-height: 350px;
  background: rgba( 0, 0, 0, 0.15 );
  box-shadow: 0 7px 30px 0 rgba( 0, 255, 0, 0.4 );
  backdrop-filter: blur( 15px );
  -webkit-backdrop-filter: blur( 4px );
  border-radius: 30px;
  color: #00ff00;
  border: 1px solid #00ff00;
  display: flex;
  transition: max-height .1s ease;
  align-items: center;
  font-size: 20px;
}

.wrapper.fogPass {
  min-height: 300px;
  height: 100%;
  max-height: 250px;
  max-width: 450px;
  width: 80%;
  transition: max-height .2s ease;
}

.wrapper.registAct {
  min-height: 500px;
  height: 100%;
  max-height: 520px;
  transition: max-height .2s ease;
}

.wrapper.welcome {
  min-height: 100px;
  height: 100%;
  max-height: 150px;
  transition: max-height .2s ease;
}


.wrapper .inbox{
  width: 100%;
  padding: 20px 40px 0px 40px;
}

.wrapper .inbox.login {
  transition: translate .18s ease;
  translate: 0;
}

.wrapper.registAct .inbox.login  {
  translate: -450px;
  transition: none
}

.wrapper.fogPass .inbox.login  {
  translate: -450px;
  transition: none
}
.wrapper.welcome .inbox.login  {
  translate: -450px;
  transition: none
}

.wrapper .inbox.registrate{
  position: absolute;
  translate: 450px;
}

.wrapper.registAct .inbox.registrate {
  transition: translate .18s ease;
  translate: 0;
}

.wrapper .inbox.fogPass{
  position: absolute;
  translate: 450px;
}

.wrapper.fogPass .inbox.fogPass {
  transition: translate .18s ease;
  translate: 0;
}

.wrapper .inbox.welcome{
  position: absolute;
  translate: 450px;
}

.wrapper.welcome .inbox.welcome {
  transition: translate .18s ease;
  translate: 0;
  
}

.legText {
  margin-left: 77%;
  cursor: default;
