}

.legText.registAct {
  margin-left: 65%;
  font-size: 1.2em;
}

.legText.fogPass {
  margin-left: 60%;
  font-size: 1em;
}
.legText.welcome {
  margin-left: 70%;
  font-size: 1.3em;
}

.inbox .input-box {
    position: relative;
    width: 100%;
    height: 50px;
    margin: 30px 0;
}


.input-box {
    border: 2px solid #00ff00;
    border-radius: 40px;
}

.input-box input {
    width: 100%;
    height: 100%;
    background: transparent;
    outline: none;
    border: none;
    font-size: 16px;
    color: rgba(0,255,0,0.7 );
    padding: 20px 45px 20px 20px;
}

.input-box input::placeholder { font-family: "Tiny5", serif; }

.inbox .remember {
    display: flex ;
    font-size: 16px;
    margin: -15px 0 15px;
  }

.remember {
    display: flex;
    
    justify-content: space-between;
    color: #00ff00;
    gap: 8px; 
  }

  .chekboxText{
    position: relative;
    margin-left: 5px;
    top: -3px;
  }
  
 
  .custom-checkbox input[type="checkbox"] {
    display: none;
  }
  
  
  .custom-checkbox .checkmark {
    width: 15px;
    height: 15px;
    background-color: black;
    border: 2px solid #00ff00;
    border-radius: 4px; 
    position: relative;
    display: inline-block;
    cursor: pointer;
    transition: background-color 0.3s, border-color 0.3s;
  }
  
  
  .custom-checkbox input[type="checkbox"]:checked + .checkmark {
    background-color: #00ff00;
    border-color: #00ff00;
  }
  
  
  .custom-checkbox .checkmark::after {
    content: '';
    position: absolute;
    display: none;
    left: 2px;
    top: 1px;
    width: 5px;
    height: 5px;
    border: solid black;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
  }
  
  
  .custom-checkbox input[type="checkbox"]:checked + .checkmark::after {
    display: block;
  }

  .fogpass {      
    position: relative; 
    text-decoration: none;
    color: yellowgreen;  
}


